body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.App-logo {
    float: left;
    animation: App-logo-spin infinite 20s linear;
    height: 45px;
    width: 45px;
}

.GitHub-logo {
    margin: 0 0 2px 0;
    height: 16px;
    width: 20px;
}

.App-header {
    height: 45px;
    background: -webkit-linear-gradient(#d6d6d8, black, white);
    color: white;
}

@keyframes App-logo-spin {
    from { transform: rotate(0deg); }
    to { transform: rotate(360deg); }
}

.center-title {
    text-align: center;
    padding-top: 7px;
    font-size: larger;
    letter-spacing: 20px;
}

.left-time {
    float: left;
    font-size: 1.0em;
    display: inline-block;
    vertical-align: top;
    padding: 10px 0 0 5px;
}

.left-menu {
    width: 240px !important;
    position: fixed;
    overflow-x: hidden;
    overflow-y: auto;
    bottom: 40px;
    top: 50px;
}

.switch-icon {
    position: absolute;
    left: 15px;
    bottom: 10px;
    z-index: 100
}

.footer {
    height: 45px;
    background: -webkit-linear-gradient(white, black, #d6d6d8);
    color: white;
    position: absolute;
    bottom: 0;
    width: 100%;
    z-index: 11
}

.ant-layout-aside {
    position: relative;
    min-height: 100%;
}

.ant-layout-aside .ant-aside-action {
    height: 42px;
    width: 224px;
    position: fixed;
    bottom: 0;
    background: #656565;
    color: #fff;
    text-align: center;
    line-height: 42px;
    cursor: pointer;
    transition: all 0.3s ease;
}

.ant-layout-aside-collapse .ant-aside-action {
    width: 64px;
    transition: all 0.3s ease;
}

.ant-layout {
    background-color: #ffffff
}
.ant-layout-footer {
    background-color: #ffffff
}
.ant-layout-sider {
    background-color: #ffffff;
    border-right: 1px solid #e8e8e8;
}
.ant-layout-sider.fixed {
    position: fixed;
    left: 0;
    z-index: 10;
    min-height: 100%;
    height: 100%;
}

.ant-menu-inline {
    border-right-width: 0px;
}

.ant-layout-sider, .ant-layout-sider-trigger {
    transition: all 0.15s cubic-bezier(0.645, 0.045, 0.355, 1);
}
.ant-layout-sider-trigger {
    line-height: 40px;
    height: 40px;
}
.ant-layout-sider-children {
    max-height: 91%;
    overflow-y:auto;
}
.ant-layout-sider-collapsed .ant-layout-sider-children{
    overflow-y:visible;
}

.ant-layout-sider-children::-webkit-scrollbar {
    width: 4px;
    height: 0px;
    background-color: transparent;
}

.ant-layout-sider-children::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 2px rgba(0,0,0,0.3);
    border-radius: 0px;
    background-color: #ffffff;
}
.ant-layout-sider-children::-webkit-scrollbar-thumb {
    border-radius: 2px;
    -webkit-box-shadow: inset 0 0 2px rgba(0,0,0,.3);
    background-color: #ccc;
}

.trigger {
    font-size: 18px;
    line-height: 50px;
    background-color: #f0f2f5;
    cursor: pointer;
    transition: color .3s;
    color: #595959;
    position: absolute;
    bottom: 4%;
}

.trigger:hover {
    color: #108ee9;
}

.content-max{
    height: 100%;
    padding: 0 10px 10px 210px;
    transition: padding 0.15s cubic-bezier(0.645, 0.045, 0.355, 1);
}

.content-normal{
    height: 100%;
    padding: 0 10px 10px 90px;
}

.width-80 {
    width: 80px;
}

.width-200 {
    width: 200px;
}

/* css for component Footprint */
.steps-content {
    position: relative;
    margin-top: 15px;
    border: 1px dashed #e9e9e9;
    border-radius: 6px;
    background-color: #fafafa;
    text-align: center;
    width: auto;
    height: 80%;
}

.steps-action {
    text-align: center;
    margin-top: 25px;
}

.typed-cursor{
    display: none;
    opacity: 1;
    animation: typedjsBlink 0.7s infinite;
    animation: typedjsBlink 0.7s infinite;
}
@keyframes typedjsBlink{
    50% { opacity: 0.0; }
}
.typed-fade-out{
    opacity: 0;
    transition: opacity .25s;
    animation: 0;
}
@font-face {
    font-family: 'hyxuejunfregular';
    src:
        url(/my-react-app/static/media/汉仪雪君体繁.0b225eaa.eot?#font-spider) format('embedded-opentype'),
        url(/my-react-app/static/media/汉仪雪君体繁.2a15844d.woff) format('woff'),
        url(/my-react-app/static/media/汉仪雪君体繁.cb2dce8c.ttf) format('truetype'),
        url(/my-react-app/static/media/汉仪雪君体繁.5af3b582.svg) format('svg');
    font-weight: normal;
    font-style: normal;
}
.typer-div {
    position: absolute;
    top: 10%;
    left: calc(100% - 53vw);
    font-size: 2vw;
    font-weight: bold;
    font-family: 'hyxuejunfregular';
}
.left70 {
    left: 70% !important;
}
.amap-logo, .amap-copyright {
    display: none !important;
}

#shapeRect {
    transition: 1s all ease;
}

#shapeRect:hover {
    stroke-dasharray: 70 0;
    stroke-dashoffset: 0;
    stroke: #06D6A0
}

@font-face {font-family: 'webfont';
    src: url('//at.alicdn.com/t/webfont_a35jl8d0gjr.eot'); /* IE9*/
    src: url('//at.alicdn.com/t/webfont_a35jl8d0gjr.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
    url('//at.alicdn.com/t/webfont_a35jl8d0gjr.woff') format('woff'), /* chrome、firefox */
    url('//at.alicdn.com/t/webfont_a35jl8d0gjr.ttf') format('truetype'), /* chrome、firefox、opera、Safari, Android, iOS 4.2+*/
    url('//at.alicdn.com/t/webfont_a35jl8d0gjr.svg#站酷小薇体') format('svg'); /* iOS 4.1- */
}

/*css for svg-clock*/
#clock {
    stroke: #ad223c;
    stroke-linecap: round;
    fill: #f2fddb;
    transition: stroke 1s ease;
}
#clock:hover {
    stroke: #adcd3c;
}
#face {
    stroke-width: 3px;
}
#ticks {
    stroke-width: 2px;
}
#hands line {
    stroke-linejoin: bevel;
}
#numbers {
    font-size: 12px;
    text-anchor: middle;
    stroke: none;
    fill:  #92b0dd;
}

/*呼吸动效*/
.double-bounce1 {
    background: #3eaeff;
    width: 65%;
    height: 65%;
    top: 17%;
    left: 18%;
}
.double-bounce2 {
    background: rgba(73,157,242,.4);
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    animation-delay: 3.8s;
}
.double-bounce1, .double-bounce2 {
    border-radius: 50%;
    position: absolute;
    animation: db 3.5s infinite cubic-bezier(.4,0,.2,1);
}



